.main_wrap{
  border: 1px solid grey;
  margin: 20px;
  display: flex;

}

.game_board {
  padding: 100px 40px;
  border-right: 1px solid black;
  width: 50%;
}

.game_board .game_menu{
  display: flex;
  justify-content: space-around;
}

.game_board .game_menu select, input, button{
  padding: 10px 20px;
  background: #CFD8DC;
  border-radius: 5px;
  color: grey;
  font-size: 18px;
  outline: none;
  border: none;
  cursor: pointer;
}

.game_board .game_menu input{
    background: #F3F3F3;
    color: grey
}

.game_board .game_menu button{
  background: #7B8D93;
  color: #fff;
  transition: .3s;
}

.game_board .game_menu button:hover{
  background: #CFD8DC;
  color: #7B8D93
}

.game_board .game{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.game_board .game p {
  text-align: center;
  font-size: 20px;
  font-weight: 500;
}

.game_board .game .dots_row{
  display: flex;
  flex-direction: row;
}

.game_board .game .dots_row .dot{
  height: 35px;
  width: 35px;
  background: #7B8D93;
  border: 1px solid black;
  cursor: pointer
}

.leaderBoard{
  padding: 4%;
}

.leaderBoard h1{
  font-size: 40px;
  font-weight: 400;
  color: grey
}

.leaderBoard div{
  padding: 10px 20px;
  background: #CFD8DC;
  color: grey;
  display: flex;
  margin-bottom: 10px;
  transition: .3s;
  border-bottom: 1px solid grey;

}
.leaderBoard div:hover{
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.8);
}

.leaderBoard p {
  font-size: 20px;
  font-weight: 400;
  margin: 0 20px;
}
.leaderBoard div p:nth-child(1){
  width: 24%;
}
.leaderBoard div p:nth-child(2) {
  margin-left: 21px;

}

